html{
  background: -webkit-linear-gradient(290deg, #A82A7E 40%, #0075BE) fixed;
  background: -o-linear-gradient(290deg, #A82A7E 40%, #0075BE) fixed;
  background: linear-gradient(160deg, #A82A7E 40%, #0075BE) fixed;
  /* background-attachment: fixed; */
  background-repeat: no-repeat;
  height: 100%;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;

}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: white;
}



